@import url('https://fonts.googleapis.com/css2?family=Inter:wght@200;300;400;500;600;700;800&family=Syne:wght@400;500;600;700&display=swap');

#Home{
    background-image: url("/public/image/bg.png");
    background-repeat: no-repeat;
  background-size:contain;
   max-height: 1000px;
   min-height: 700px;
    width: auto;
  
    padding: 15px;
    margin-top: 75px;
    
}
#Home h1{
    font-family: "Syne";
    font-size: 3.2rem;
    font-weight: 600;
    margin-bottom: 5px;
    color: #261e45;
}
.header{
    width: 80%;
    margin:  auto;
}
.btnApp Button:hover{
    background-color: black;
    color: white;
}
.btnApp Button{
    background-color:white; color:#261e45;
    padding: 8px 8px;
    font-size: 1.2rem;
}
.headerImage{
padding: 25px;
margin: auto;
}
.headerImage img{
    width:80%;
    height: auto;
    text-align: center;
    border: 2px solid white;
    border-radius: 0 65px 0 65px;box-shadow: 1px 2px 6px black;
    margin: auto;
}